<template>
	<div class="p">
		<div class="left-bg">
			<div class="park-total">
				// <img src="../assets/img/zxsg/yxqk.png"/>
				<div class="my-table">
					<div class="table-title">
						<ul>
							<li>
								<label>区域</label>
								<label style="width:60px;">电信</label>
								<label style="width:60px;">移动</label>
								<label style="width:55px;">联通</label>
							</li>
						</ul>
					</div>
					<div class="table-content">
						<ul>
							<li v-for="(item,index) in runData">
								<label>{{item.area}}</label>
								<label :style="{color:item.telecom=='异常'?'red':'#fff'}">{{item.telecom}}</label>
								<label :style="{color:item.mobile=='异常'?'red':'#fff'}">{{item.mobile}}</label>
								<label :style="{color:item.unicom=='异常'?'red':'#fff'}">{{item.unicom}}</label>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>	
</template>

<script>
//	系统预警
	export default{
		components:{
		},
		data(){
			return{
				runData:[{
					area:'天府软件园A区',
					telecom:'正常',
					mobile:'正常',
					unicom:'正常'
				},{
					area:'天府软件园B区',
					telecom:'正常',
					mobile:'正常',
					unicom:'异常'
				},{
					area:'天府软件园C区',
					telecom:'正常',
					mobile:'异常',
					unicom:'正常'
				},{
					area:'天府软件园D区',
					telecom:'异常',
					mobile:'正常',
					unicom:'正常'
				},{
					area:'天府软件园E区',
					telecom:'正常',
					mobile:'正常',
					unicom:'正常'
				},{
					area:'天府软件园F区',
					telecom:'正常',
					mobile:'正常',
					unicom:'异常'
				},{
					area:'天府软件园G区',
					telecom:'正常',
					mobile:'异常',
					unicom:'正常'
				}]
			}

		},
		methods:{

		}
	}
</script>

<style scoped="scoped" lang="less">
	.my-table{
		display: table;
		width: 100%;
		margin-top: 40px;
		ul{
			li{
				box-sizing: border-box;
				display:table;
				width: 390px;
				color: #FFFFFF;
				list-style: none;
				label,span{
					padding: 0 5px;
					display: table-cell;
				}	
				padding:0;
				margin:0;
			}
			padding:0;
			margin:0;
		}
		.table-title{
			color: #FFFFFF;
			line-height:45px;
			display: table;
			width: 100%;
			background: rgba(0,85,189,.53);
			
		}
		.table-content{
			li{
				padding-bottom: 10px;
				padding-top: 10px;
				border-bottom: 1px solid rgba(255,255,255,.24);
			}
			big{
				font-size: 36px;
				vertical-align: text-bottom;
				line-height: 36px;
			}
			small{
				font-size: 16px;
				vertical-align: text-bottom;
			}
		}
	}
</style>


